a {
  background-color: #f9f9f9;
  padding: 0.25em 0.5em;
  margin: 3px;
  border-radius: 2px;
  display: inline-block;
}

a.disabled {
  background-color: #aaaaaa;
  color: #555555 !important;
  padding: 0.25em 0.5em;
  margin: 3px;
  border-radius: 2px;
}

a.disabled:hover {
  background-color: #aaaaaa !important;
  cursor: not-allowed;
}

a.back::before {
  border-style: solid;
  border-width: 0.14em 0.14em 0 0;
  content: "";
  display: inline-block;
  height: 0.45em;
  left: 0.25em;
  transform: rotate(-135deg);
  position: relative;
  top: 0.35em;
  vertical-align: top;
  width: 0.45em;
  margin-right: 0.3em;
}

a.back:empty {
  margin-right: 0;
  padding: 4px 10px;
}

a.round {
  border-radius: 50%;
  border: 2px solid lightgray;
  position: relative;
}

a.round:hover {
  background-color: #eee;
  border: 2px solid lightgray !important;
  position: relative;
}

a.pick {
  box-shadow: 0 5px 0 gray;
}

a.pick:hover {
  background-color: #eee;
}

/* Modeled after https://loading.io/css/#lds-dual-ring */
.loading {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.loading:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid gray;
  border-color: gray transparent gray transparent;
  animation: loading 1.2s linear infinite;
}
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-options {
  margin: 0.5em;
}

.chevron::before {
  border-style: solid;
  border-width: 0.15em 0.15em 0 0;
  content: "";
  display: inline-block;
  height: 0.45em;
  left: 0.15em;
  position: relative;
  top: 0.35em;
  transform: rotate(-45deg);
  vertical-align: top;
  width: 0.45em;
}

.chevron.right:before {
  left: 0;
  transform: rotate(45deg);
}

.chevron.bottom:before {
  top: 0;
  transform: rotate(135deg);
}

.chevron.left:before {
  left: 0.25em;
  transform: rotate(-135deg);
}
